#Sub_Community {
	.wrap {
		.contents {
			width: 100%;
			height: 100%;
			overflow: hidden;

			.Insert_Area {
				width: 100%;
				border: 1px solid #d2d2d2;
				padding: 40px 20px 0px;
				margin-bottom: 30px;

				.input_Area {
					width: 100%;
					text-align: left;
					position: relative;
					margin: 15px 0;
					cursor: pointer;

					input {
						font-size: 15px;
						color: #1e1e2f;
						width: 100%;
						border: none;
						border-bottom: solid #aaaaaa 1px;
						padding-bottom: 10px;
						padding-left: 10px;
						position: relative;
						background: none;
						margin-right: 10px;

						&::placeholder {
							color: #aaaaaa;
						}
						&:focus {
							outline: none;
						}

						&:focus ~ label,
						&:valid ~ label {
							font-size: 16px;
							bottom: 40px;
							color: #666;
							font-weight: bold;
						}

						&:focus ~ span,
						&:valid ~ span {
							width: 100%;
						}
					}

					label {
						position: absolute;
						color: #aaa;
						left: 10px;
						font-size: 20px;
						bottom: 8px;
						transition: all 0.2s;
					}
				}

				textarea {
					width: 100%;
					height: auto;
					padding: 15px;
					color: #1e1e2f;
					font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
					font-weight: 400;
					resize: none;
					margin-top: 15px;
					outline: none;
					border: 1px solid #aaa;
				}

				.btn_Area {
					padding: 15px 0;
					button {
						width: 100px;
						height: 30px;
						font-size: 17px;
						border: 2px solid #1e1e2f;
						font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
						font-weight: 500;
						background: transparent;
						cursor: pointer;
						transition: all 0.3s ease;
						position: relative;
						display: inline-block;
						border-radius: 10px;
						margin-right: 20px;

						&:after {
							position: absolute;
							content: ' ';
							top: 0;
							left: 0;
							z-index: -1;
							width: 100%;
							height: 100%;
							transition: all 0.3s ease;
							transform: scale(0.1);
						}
						&:hover {
							color: #fff;
						}
						&:hover:after {
							background: #000;
							transform: scale(1);
						}
					}
				}
			}

			.Show_Area {
				width: 100%;
				border: 1px solid #d2d2d2;
				padding: 40px 20px;

				article {
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding: 20px 0;
					background: #fff;
					margin-bottom: 30px;

					&:last-child {
						margin-bottom: 0px;
					}

					.txt {
						width: 70%;
						padding: 30px;

						.input_Area {
							width: 100%;
							text-align: left;
							position: relative;
							margin: 15px 0;
							cursor: pointer;

							input {
								font-size: 15px;
								color: #1e1e2f;
								width: 100%;
								border: none;
								border-bottom: solid #aaaaaa 1px;
								padding-bottom: 10px;
								padding-left: 10px;
								position: relative;
								background: none;
								margin-right: 10px;

								&::placeholder {
									color: #aaaaaa;
								}
								&:focus {
									outline: none;
								}

								&:focus ~ label,
								&:valid ~ label {
									font-size: 16px;
									bottom: 40px;
									color: #666;
									font-weight: bold;
								}

								&:focus ~ span,
								&:valid ~ span {
									width: 100%;
								}
							}

							label {
								position: absolute;
								color: #aaa;
								left: 10px;
								font-size: 20px;
								bottom: 8px;
								transition: all 0.2s;
							}
						}

						textarea {
							width: 100%;
							height: auto;
							padding: 15px;
							color: #1e1e2f;
							font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
							font-weight: 400;
							resize: none;
							margin-top: 15px;
							outline: none;
							border: 1px solid #aaa;
						}

						h2 {
							padding: 10px 0;
							font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
							font-weight: 700;
							font-size: 24px;
						}

						p {
							font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
							font-weight: 400;
							font-size: 16px;
						}
					}
					.btnSet {
						width: 10%;
						display: flex;
						justify-content: center;
						gap: 10px;
						flex-wrap: wrap;
						margin-right: 15px;

						button {
							width: 100px;
							height: 30px;
							font-size: 17px;
							border: 2px solid #1e1e2f;
							font-family: 'Poppins', Helvetica, Arial, Lucida, sans-serif;
							font-weight: 500;
							background: transparent;
							cursor: pointer;
							transition: all 0.3s ease;
							position: relative;
							display: inline-block;
							border-radius: 10px;

							&:after {
								position: absolute;
								content: ' ';
								top: 0;
								left: 0;
								z-index: -1;
								width: 100%;
								height: 100%;
								transition: all 0.3s ease;
								transform: scale(0.1);
							}
							&:hover {
								z-index: 1;
								color: #fff;
							}
							&:hover:after {
								background: #000;
								transform: scale(1);
							}
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	#Sub_Community {
		.wrap {
			.contents {
				.Insert_Area {
					.input_Area {
						input {
							font-size: 12px;

							&:focus ~ label,
							&:valid ~ label {
								font-size: 12px;
							}
						}

						label {
							font-size: 14px;
						}
					}

					textarea {
						font-size: 12px;
					}

					.btn_Area {
						padding: 15px 0;
						button {
							width: 80px;
							height: 30px;
							font-size: 13px;
							margin-right: 15px;
						}
					}
				}

				.Show_Area {
					padding: 40px 20px;

					article {
						flex-wrap: wrap;
						padding: 20px 0;
						margin-bottom: 30px;

						.txt {
							width: 100%;
							padding: 30px;

							.input_Area {
								input {
									font-size: 12px;

									&:focus ~ label,
									&:valid ~ label {
										font-size: 12px;
									}
								}

								label {
									font-size: 14px;
								}
							}

							textarea {
								font-size: 12px;
							}

							h2 {
								font-size: 16px;
							}

							p {
								font-size: 12px;
							}
						}
						.btnSet {
							width: 100%;
							justify-content: flex-end;
							margin-right: 15px;

							button {
								width: 80px;
								height: 30px;
								font-size: 13px;
								margin-right: 0px;
							}
						}
					}
				}
			}
		}
	}
}
